
img[data-v-c18b5000] {
	width: 100%;
}
.wrap[data-v-c18b5000]{
	width: 100%;
	height: 16.453333rem;
	background: url(../../../static/img/pointbg.png) no-repeat;
	background-size: 100% 100%;
	position: relative;
	overflow: hidden;
}
.head[data-v-c18b5000]{
	width: 80%;
	background: #91755c;
	line-height: 3.333333rem;
	text-align: center;
	position: relative;
	margin: 0 auto;
	margin-top: 3.666666rem;
	font-size: 0.426666rem;
	border-radius: 0.4rem;
	color:white;
}
.head img[data-v-c18b5000]{
	width: 2.666666rem;
	position: absolute;
	top: -1.333333rem;
	left: 50%;
	margin-left: -1.333333rem;
}
.scan[data-v-c18b5000]{
	margin-top:1.6rem;
	width: 100%;
	text-align: center;
}
.scan p[data-v-c18b5000]{
	font-size: 0.46rem;
	color: white;
	margin-bottom: 0.666666rem;
}
/*手指*/
.hand[data-v-c18b5000]{
	position: absolute;
	right: 0.133333rem;
	top: 0.133333rem;
	width: 1.6rem;
}
.hand img[data-v-c18b5000]{
	-webkit-transform: rotateZ(45deg);
	        transform: rotateZ(45deg);
}
.handmove[data-v-c18b5000]{
	-webkit-animation: handmove-data-v-c18b5000 1s linear infinite alternate;
	        animation: handmove-data-v-c18b5000 1s linear infinite alternate;
}
@-webkit-keyframes handmove{
from{
		opacity: 0.5;
		-webkit-transform: translate3D(10px,-10px,0);
		        transform: translate3D(10px,-10px,0);
}
to{
		opacity: 1;
		-webkit-transform: translateY(0px);
		        transform: translateY(0px);
}
}
@keyframes handmove-data-v-c18b5000{
from{
		opacity: 0.5;
		-webkit-transform: translate3D(10px,-10px,0);
		        transform: translate3D(10px,-10px,0);
}
to{
		opacity: 1;
		-webkit-transform: translateY(0px);
		        transform: translateY(0px);
}
}



